<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        sm12
        md9
        lg8
      >
        <v-toolbar
          dark
          color="primary"
        >
          <v-toolbar-title>ของดีบนดอย Login</v-toolbar-title>
        </v-toolbar>
        <v-row>
          <v-alert
            type="error"
            color="red lighten-1"
            v-if="isEmtyp"
          >
            ไม่สามารถเข้าสู่ระบบได้ กรุณาตรวจสอบ Username หรือ Password
          </v-alert>
        </v-row>
        <v-container class="grey lighten-5">
          <v-row no-gutters>
            <v-col
              cols="12"
              sm="6"
              md="5"
            >
              <v-card>
                <v-img
                  class="white--text"
                  height="300px"
                  :src="require('../assets/img/home/login-logo.jpeg')"
                >
                </v-img>
              </v-card>
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-card-text>
                <v-form>
                  <v-text-field
                    prepend-icon="mdi-account"
                    name="login"
                    label="Login"
                    type="text"
                    v-model="username"
                    :rules="usernameRules"
                  ></v-text-field>
                  <v-text-field
                    id="password"
                    prepend-icon="mdi-lock"
                    name="password"
                    label="Password"
                    type="password"
                    v-model="password"
                    :rules="passwordRules"
                  ></v-text-field>
                </v-form>
              </v-card-text>

              <v-row class="ml-2">
                <v-col>
                  <router-link to="/login">
                    ลืมรหัสผ่าน
                  </router-link>
                  |
                  <router-link to="/login">
                    ไม่ได้รับอีเมลล์หรือข้อความยืนยัน
                  </router-link>
                </v-col>
              </v-row>

              <v-row
                class="ml-1"
                justify="center"
              >
                <v-card-actions>
                  <v-btn
                    class="mr-1"
                    color="primary"
                    @click="checkLogin"
                  >
                    <v-icon>mdi-login</v-icon>เข้าสู่ระบบ
                  </v-btn>

                  <v-btn
                    class="ml-1"
                    color="primary"
                    to="/register"
                  >
                    <v-icon>mdi-pen</v-icon>สมัครสมาชิก
                  </v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-row>

            </v-col>
            <v-col
              justify="center"
              cols="12"
              md="3"
            >
              <v-card-text>
                <v-row cols="12" justify="center">
                  <v-icon class="mr-1">mdi-account-details</v-icon>
                  <h3>ขั้นตอนการสมัครสมาชิก</h3>
                </v-row>
                <v-row justify="center">
                  <ol class="mt-3 font-weight-bold body-2">
                    <li>คลิกเมนู "สมัครสมาชิก"</li>
                    <li class="mt-1">เลือกประเภทสมาชิก ที่ต้องการสมัคร</li>
                    <li class="mt-1">กรอกข้อมูล</li>
                    <li class="mt-1">คลิกปุ่ม "ยอมรับข้อตกลง"</li>
                    <li class="mt-1">คลิกปุ่ม "สมัครสมาชิก"</li>
                    <li class="mt-1">ยืนยันตัวตน</li>
                    <li class="mt-1">รอผู้ดูแลระบบ อนุมัติ</li>
                  </ol>
                </v-row>
              </v-card-text>
            </v-col>
          </v-row>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    valid: false,
    username: "",
    isEmtyp: false,
    usernameRules: [(v) => !!v || "Username is required"],
    password: "",
    passwordRules: [(v) => !!v || "Password is required"],
  }),
  methods: {
    checkLogin() {
      this.isEmtyp = false;

      if (
        this.username === "" ||
        this.username === null ||
        this.username === undefined
      ) {
        this.isEmtyp = true;
        return;
      }

      if (
        this.password === "" ||
        this.password === null ||
        this.password === undefined
      ) {
        this.isEmtyp = true;
        return;
      }
    },
  },
};
</script>
<style scoped>
#rounded {
  border-radius: 5px;
}
</style>



